// ------------------------------
// mixins
// ------------------------------
@mixin twoCol {
    overflow: hidden; }
@mixin twoColFixed {
    float: left;
    margin-right: 10px; }

@mixin twoColFixedRight {
    @include twoColFixed;
    float: right;
    margin-left: 10px;
    margin-right: 0; }

@mixin twoColFluid {
    overflow: hidden;
    margin-left: 0;

    p:first-child {
        margin-top: 0; } }

// ------------------------------
// classes
// ------------------------------
.l {
    &-twoCol {
        @include twoCol;

        &__fluid {
            @include twoColFluid; }

        &__fixed {
            @include twoColFixed; }

        &--right {
            @include twoColFixedRight; } } }

