.u {
    &-is {
        &--pc {
            @include tablet {
                display: none !important; } }

        &--sp {
            display: none !important;

            @include tablet {
                display: inherit !important; } } }


    &--inner {
        &-tablet {
            @include tablet {
                margin: {
                    left: 10px;
                    right: 10px; } } }

        &-sp {
            @include smartphone {
                margin: {
                    left: 10px;
                    right: 10px; } } } } }
