@charset "UTF-8";
/* Reset Style */
html {
  overflow-y: scroll;
}

body {
  line-height: 1;
  color: #000;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

body {
  font: 13px/1.231 arial,helvetica,clean,sans-serif;
  *font-size: small;
  *font: x-small;
}

select, input, button, textarea {
  font: 99% arial,helvetica,clean,sans-serif;
}

table {
  font-size: inherit;
  font: 100%;
}

pre, code, kbd, samp, tt {
  font-family: monospace;
  *font-size: 108%;
  line-height: 100%;
}

absorb--absorb {
  margin-top: 0;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 13px;
}

body {
  box-sizing: border-box;
  background: #fff;
  color: #000;
  line-height: 1.6;
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
}

a, a:visited {
  color: #000;
  text-decoration: none;
}

a:hover, a:focus, a:active, a:visited:hover, a:visited:focus, a:visited:active {
  color: #000;
}

img {
  vertical-align: bottom;
  max-width: 100%;
  max-height: 100%;
}

strong {
  font-weight: bold;
}

p {
  margin: 10px 0 0;
}

blockquote {
  padding: 5px 1em;
  border-left: 4px solid #efefef;
}

blockquote > *:first-child {
  margin-top: 0;
}

pre {
  padding: 0.4em;
  background: #f9f9f9;
  color: #644;
}

h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.5;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  margin: .8em 0;
}

h1 {
  font-size: 1.6em;
}

h2 {
  font-size: 1.4em;
}

h3 {
  font-size: 1.2em;
}

h4 {
  font-size: 1em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 1em;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul li {
  list-style-type: none;
}

ol {
  list-style: none;
  counter-reset: li;
}

ol li {
  position: relative;
  padding-left: 20px;
}

ol li:before {
  content: counter(li) ".";
  counter-increment: li;
  padding-right: 6px;
  margin-left: -20px;
}

input, textarea {
  box-sizing: border-box;
}

input[type=text],
input[type=password],
input[type=tel],
input[type=email],
input[type=number] {
  padding: 3px;
  border: 1px solid #999;
  box-shadow: none;
}

input[type=checkbox] {
  margin-right: 10px;
}

select {
  width: 100%;
  padding: 0;
  border-radius: 0;
  height: 1.6em;
  border: 1px solid #999999;
  font-size: .8em;
}

.u-align--left {
  text-align: left;
  margin-right: auto;
}

.u-align--right {
  text-align: right;
  margin-left: auto;
}

.u-align--center {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.u-align--vertical-middle {
  vertical-align: middle;
}

.u-align--vertical-base {
  vertical-align: baseline;
}

